@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200&display=swap);
body, html {
  scroll-behavior: smooth;
  margin: 0;
  width: 100%;
  font-family: 'Raleway', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root{
--mainColor: #fff;
--primaryFocus: rgb(163, 56, 95);
--primaryFocusTranparent: rgba(163, 56, 95, .7);
--secondaryFocus: #9ad5ca;
--secondaryFocusTranparent: rgba(154, 213, 202, .6);
--details:#4A7B9D;
--lightText: #fff;
--darkText: #000000;
--borders: #dadce0;
}
.App {
  position: relative;

  max-width: 100vw;

  font-size: 15px;
  font-weight: bold;
  overflow-x: hidden;
}

a {
  text-decoration: underline;
  color: var(--primaryFocus);

  cursor: pointer;
}

/* ------------------- TABLET/MOBILE SCREENS - SMALLER THAN 769px -------------------- */

@media (max-width:769px) {
  .App {
    padding-bottom: 151px;
  }
}

/* ----------------- SCREENS SMALLER THAN 490px  ------------------- */

@media (max-width: 490px) {
  .App {
    padding-bottom: 121px;
    font-size: 12px;
  }
}
section {
  display: grid;
  grid-template-columns: 33% 66%;

  min-height: 80vh;
  padding: 6em 0;
}

section .title {
  text-align: right;
  padding-right: 1em;
  margin-right: 1em;
  border-right: 1px solid var(--borders);
}

section .content {
  line-height: 1.7;
}

/* ----------------- SCREENS LARGER THAN 1200px ------------------ */

@media (min-width: 1200px) {
  section {
    min-height: auto;
  }

  section .content {
    max-width: 680px;
  }
}

/* ---------------- SCREENS SMALLER THAN 1180px ---------------------- */

@media (max-width: 1180px) {
  section {
    padding: 5em 6em;
  }

  /* changing sections to column direction */
  section .content {
    display: flex;
    flex-direction: column;
    align-content: center;
    text-align: left;
    justify-content: center;

    padding-right: 1.6em;
  }
}

/* ------------------- TABLET/MOBILE SCREENS - SMALLER THAN 769px -------------------- */

@media (max-width:769px) {
  section {
    position: relative;

    padding: 2em 5em;

    /* titles to be above the section */
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  section .title {
    width: auto;
    margin: 0;

    align-content: center;
    padding: 1.5em 1.5em .8em 1.5em;
    border-right: none;
    border-bottom: 1px solid var(--borders);
  }

  section .content {
    width: auto;
    text-align: justify;
    padding: 1em 0 0 0;
  }
}

